<template>
  <v-container fluid>
    <div class=" red darken-4 white--text text-center ma-10">
      <v-row class="mt-2 pa-5 pb" align="center" text-center>
        <v-col cols="12" md="3">
          <span
            style="font-size: 1.25rem;font-weight: 500;letter-spacing: 0.0125em;"
          >
            FIRMEN ANTRÄGE
          </span></v-col
        >
        <v-col cols="12" md="4">
          <v-text-field
            v-model="search"
            hide-details
            label="Nach Firma suchen..."
            solo
          ></v-text-field>
        </v-col>
        <v-col cols="12" md="3">
          <v-autocomplete
            v-model="status"
            :items="statuse"
            hide-details
            chips
            small-chips
            label="Status filtern"
            multiple
            solo
          ></v-autocomplete>
        </v-col>
        <v-col>
          <v-dialog v-model="dialog" max-width="1000">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                v-bind="attrs"
                v-on="on"
                icon
                :disabled="selected.length == 0"
              >
                <v-icon color="white" x-large>mdi-email-plus</v-icon>
              </v-btn>
            </template>
            <SendEmail
              :selectedEmails="selected"
              @closeDialog="closeDialog()"
              @deSelectFirma="deSelectFirma"
            />
          </v-dialog>
        </v-col>
      </v-row>
    </div>
    <v-card
      class="ma-10 center"
      min-height="500"
      v-if="filterAnträge.length == 0"
    >
      <div>
        <p><i>No data available</i></p>
      </div>
    </v-card>
    <v-card class="ma-10" min-height="500" v-else>
      <v-simple-table class="ma-5 ">
        <template v-slot:default>
          <thead>
            <tr>
              <th class="text-center">
                <v-checkbox
                  @click="SelectAll()"
                  v-model="allSelected"
                ></v-checkbox>
              </th>
              <th class="text-center">
                Name
              </th>
              <th class="text-center">
                Email
              </th>
              <th class="text-center">
                Datum
              </th>
              <th class="text-center">
                Status
              </th>
              <th class="text-center">
                Action
              </th>
            </tr>
          </thead>

          <tbody class="text-center">
            <template v-for="item in filterAnträge">
              <tr :key="item.firmen_id">
                <td>
                  <v-checkbox
                    v-model="selected"
                    :value="item"
                    @click="allSelected = false"
                  ></v-checkbox>
                </td>
                <td>{{ item.firmen_name }}</td>
                <td>{{ item.firmen_mail }}</td>
                <td>{{ item.anfrage_zeitpunkt.substring(0, 10) }}</td>
                <td v-if="item.status == 'Anfrage'">
                  <v-chip color="primary" dark small>{{ item.status }}</v-chip>
                </td>
                <td v-if="item.status == 'Abgelehnt'">
                  <v-chip color="error" small>{{ item.status }}</v-chip>
                </td>
                <td v-if="item.status == 'Teilnehmer'">
                  <v-chip color="success" small>{{ item.status }}</v-chip>
                </td>
                <td v-if="item.status == 'Daten erhalten'">
                  <v-chip color="blue-grey" class="white--text" small>{{
                    item.status
                  }}</v-chip>
                </td>
                <td class="px-5">
                  <div v-if="item.status == 'Teilnehmer'">
                    <v-dialog max-width="600px">
                      <template v-slot:activator="{ on, attrs }">
                        <v-btn icon v-bind="attrs" v-on="on">
                          <v-icon> mdi-forwardburger</v-icon>
                        </v-btn>
                      </template>
                      <ShowData
                        @close="dialogData = false"
                        :user="item"
                        :dialogData="dialogData"
                      />
                    </v-dialog>
                  </div>
                  <div
                    v-if="
                      item.status != 'Abgelehnt' &&
                        item.status != 'Teilnehmer' &&
                        item.status != 'Daten erhalten'
                    "
                  >
                    <v-btn icon @click="changeStatus('Abgelehnt', item)">
                      <v-icon color="red darken-4" small
                        >mdi-archive-remove-outline</v-icon
                      ></v-btn
                    >
                    <v-btn icon @click="changeStatus('Daten erhalten', item)"
                      ><v-icon color="primary" dark small
                        >mdi-email-check-outline</v-icon
                      ></v-btn
                    >
                  </div>
                </td>
              </tr>
              <!-- <tr
                v-if="opened.includes(item.firmen_id)"
                :key="item.firmen_id"
                class="pa-10"
              >
                <td>Lorem Ipsum</td>
                <td>Lorem Ipsum!</td>
                <td>Lorem Ipsum!</td>
                <td>Lorem Ipsum!</td>
                <td>Lorem Ipsum!</td>
                <td>Lorem Ipsum!</td>
                <v-divider dark></v-divider>
              </tr> -->
            </template>
          </tbody>
        </template>
      </v-simple-table>
    </v-card>
    <v-snackbar v-model="snackbar" :timeout="timeout" :color="snackbarcolor">
      {{ message }}

      <template v-slot:action="{ attrs }">
        <v-btn color="white" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
  </v-container>
</template>

<script>
import axios from 'axios';
import SendEmail from '@/components/SendEmail.vue';
import ShowData from '@/components/ShowData.vue';
export default {
  components: {
    SendEmail,
    ShowData,
  },
  data() {
    return {
      search: '',
      message: '',
      snackbar: false,
      timeout: 5000,
      selected: [],
      calories: '',
      status: [],
      snackbarcolor: 'none',
      statuse: ['Anfrage', 'Abgelehnt', 'Teilnehmer', 'Daten erhalten'],
      dialog: false,
      allSelected: false,
      dialogData: false,
    };
  },
  props: {
    antraege: {
      type: Array,
    },
  },
  computed: {
    headers() {
      return [
        {
          text: 'Name',
        },
        {
          text: 'Email',
        },
        { text: 'Anfrage Datum' },
        { text: 'Status' },
      ];
    },

    filterAnträge() {
      if (this.status.length > 0) {
        return this.antraege.filter(
          (el) =>
            el.firmen_name.toLowerCase().includes(this.search.toLowerCase()) &&
            this.status.includes(el.status),
        );
      } else {
        return this.antraege.filter((el) =>
          el.firmen_name.toLowerCase().includes(this.search.toLowerCase()),
        );
      }
    },
  },
  methods: {
    // getVerlauf() {
    //   let verlauf = JSON.parse(localStorage.getItem('user'));
    //   if (user != null) {
    //     this.verlauf = verlauf;
    //   }
    // },
    async changeStatus(status, item) {
      await axios({
        url: '/status/' + item.firmen_id,
        method: 'PATCH',
        contentType: 'application/json',
        data: {
          status: status,
        },
      });
      this.snackbar = true;
      this.$emit('refreshAntraege');

      switch (status) {
        case 'Abgelehnt':
          this.$emit('sendmail', {
            firmen_name: item.firmen_name,
            email: item.firmen_mail,
            type: 'abgelehnt',
          });
          this.message = 'Antrag abgelehnt!';
          this.snackbarcolor = 'red darken-4';
          break;
        case 'Daten erhalten':
          const { data } = await axios({
            url: '/setanmeldedaten',
            method: 'POST',
            contentType: 'application/json',
            data: {
              firmen_id: item.firmen_id,
            },
          });
          this.$emit('sendmail', {
            firmen_name: item.firmen_name,
            email: item.firmen_mail,
            type: 'daten',
            user_id: data.user_id,
            passwort: data.passwort,
          });

          this.message = 'Antrag angenommen!';
          this.snackbarcolor = 'success';
          break;
        default:
          this.message = 'Fehler';
          this.snackbarcolor = 'red';
      }
    },

    SelectAll() {
      this.selected = [];
      if (this.allSelected) {
        for (const item of this.filterAnträge) {
          if (this.selected.find((el) => el == item) == null) {
            this.selected.push(item);
          }
        }
      }
    },
    deSelectFirma(item) {
      this.selected = this.selected.filter((el) => el != item);
    },
    closeDialog() {
      this.dialog = false;
      this.allSelected = false;
      this.selected = [];
    },
    // async sendEmailWithStatus(firma, status) {
    //   let message = '';
    //   if (status == 'Abgelehnt') {
    //     message = `Liebes ${firma.firmen_name} ${this.abgelehntMessage}`;
    //   }
    //   if (status == 'Teilnehmer') {
    //     message = `Liebes ${firma.firmen_name} ${this.teilnehmerMessage}`;
    //   } else {
    //     console.log('Fehler');
    //   }
    //   const finalMessage = message.replaceAll('\n', '<br/>\r\n');

    //   await axios({
    //     url: '/sendmail',
    //     method: 'POST',
    //     contentType: 'application/json',
    //     data: {
    //       emails: firma.firmen_mail,
    //       subject: 'Karrieretag',
    //       message: finalMessage,
    //     },
    //   });
    // },
  },
};
</script>

<style lang="scss" scoped></style>
