<template>
  <v-card class="mx-auto" max-width="1000" elevation="0">
    <v-toolbar dark color="transparent">
      <v-toolbar-title class="black--text">Activities</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon color="black" to="/activities">
        <v-icon>mdi-menu</v-icon>
      </v-btn>
    </v-toolbar>
    <v-sheet
      class="v-sheet--offset mx-auto"
      color="cyan"
      max-width="calc(100% - 32px)"
    >
    </v-sheet>
    <div style="max-height:40vh; overflow:auto">
      <div class="ma-5">
        <v-list v-for="(h) in limitActivities" :key="h.history_id" class="pa-0">
          <v-list-item>
            <v-list-item-avatar>
              <v-icon
                v-if="h.type == 'formular' || h.type == 'Formular'"
                class="red darken-4"
                dark
                >mdi-clipboard-text</v-icon
              >
              <v-icon v-else class="blue" dark>mdi-gesture-tap-button</v-icon>
            </v-list-item-avatar>
  
            <v-list-item-content>
              <v-list-item-title v-text="h.firmen_name"></v-list-item-title>
            </v-list-item-content>
  
            <v-list-item-action>
              <v-list-item-subtitle v-if="h.type == 'login' || h.type == 'Login'"
                >Das erste Mal Angemeldet</v-list-item-subtitle
              >
              <v-list-item-subtitle v-else
                >Formular ausgefüllt</v-list-item-subtitle
              >
            </v-list-item-action>
          </v-list-item>
        </v-list>
      </div>
    </div>
    <v-card-text class="pt-4">
      <div class="text-h6 font-weight-light mb-2">
        Last company activities
      </div>
      <v-divider class="my-2"></v-divider>
      <v-icon class="mr-2" small>
        mdi-clock
      </v-icon>
      <span class="text-caption grey--text font-weight-light"
        >last on {{ lastRequest }}</span
      >
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  computed: {
    limitActivities() {
      return this.limit
        ? this.activities.slice(0, this.limit)
        : this.activities;
    },
  },
  data() {
    return {
      lastRequest: this.activities[0].time.substring(0, 10),
      limit: 4,
    };
  },
  props: {
    activities: {
      type: Array,
    },
  },
};
</script>

<style scoped></style>
