<template>
  <v-card class="mx-auto" max-width="1000" elevation="0">
    <v-toolbar dark color="transparent">
      <v-toolbar-title class="black--text">Request</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn icon color="black" to="/requests">
        <v-icon>mdi-menu</v-icon>
      </v-btn>
    </v-toolbar>
    <v-sheet
      class="v-sheet--offset mx-auto"
      color="cyan"
      max-width="calc(100% - 32px)"
    >
    </v-sheet>
    <div style="max-height:40vh; overflow:auto">
      <v-simple-table class="ma-5 ">
        <template v-slot:default>
          <tbody class="text-center">
            <template v-for="item in limitAnträge">
              <tr :key="item.firmen_id">
                <td>{{ item.firmen_name }}</td>
                <td v-if="item.status == 'Anfrage'">
                  <v-chip color="primary" dark small>{{ item.status }}</v-chip>
                </td>
                <td v-if="item.status == 'Abgelehnt'">
                  <v-chip color="error" small>{{ item.status }}</v-chip>
                </td>
                <td v-if="item.status == 'Teilnehmer'">
                  <v-chip color="success" small>{{ item.status }}</v-chip>
                </td>
                <td v-if="item.status == 'Daten erhalten'">
                  <v-chip color="blue-grey" class="white--text" small>{{ item.status }}</v-chip>
                </td>
              </tr>
            </template>
          </tbody>
        </template>
      </v-simple-table>
    </div>
    <v-card-text class="pt-0">
      <div class="text-h6 font-weight-light mb-2">
        Last company requests
      </div>
      <v-divider class="my-2"></v-divider>
      <v-icon class="mr-2" small>
        mdi-clock
      </v-icon>
      <span class="text-caption grey--text font-weight-light"
        >last on {{ lastRequest }}</span
      >
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  computed: {
    limitAnträge() {
      return this.limit ? this.antraege.slice(0, this.limit) : this.antraege;
    },
  },
  data() {
    return {
      lastRequest: this.antraege[0].anfrage_zeitpunkt.substring(0, 10),
      limit: 5,
    };
  },
  props: {
    antraege: {
      type: Array,
    },
  },
};
</script>

<style scoped></style>
