<template>
  <v-col cols="12" class="pa-0">
    <v-card>
      <v-card-title class="red darken-4">
        <span class="text-h5 white--text">{{ user.firmen_name }}</span>

        <v-spacer></v-spacer>

        <v-btn dark icon text @click="$emit('close')">
          <v-icon>mdi-close</v-icon>
        </v-btn>
      </v-card-title>
      <v-list>
        <v-list-item>
          <v-list-item-action>
            <v-icon>mdi-account-tie-outline</v-icon>
          </v-list-item-action>

          <v-list-item-content>
            <v-list-item-title>{{
              user.ausstellung_wie_homepage
            }}</v-list-item-title>
          </v-list-item-content>
          <v-list-item-action>
            <v-icon>mdi-message-text</v-icon>
          </v-list-item-action>
        </v-list-item>

        <v-divider inset></v-divider>

        <v-list-item>
          <v-list-item-action>
            <v-icon>mdi-email</v-icon>
          </v-list-item-action>

          <v-list-item-content>
            <v-list-item-title>{{ user.firmen_mail }}</v-list-item-title>
          </v-list-item-content>

          <v-list-item-action>
            <v-icon>mdi-message-text</v-icon>
          </v-list-item-action>
        </v-list-item>

        <v-divider inset></v-divider>
        <v-list-item>
          <v-list-item-action>
            <v-icon>mdi-sign-direction</v-icon>
          </v-list-item-action>

          <v-list-item-content>
            <v-list-item-title>{{
              getFachrichtung(user.fachrichtung)
            }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>

        <v-divider inset></v-divider>

        <v-list-item>
          <v-list-item-action>
            <v-icon>mdi-bag-suitcase</v-icon>
          </v-list-item-action>

          <v-list-item-content>
            <v-list-item-title>Am {{ user.platz }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider inset></v-divider>
        <v-list-item>
          <v-list-item-action>
            <v-icon>mdi-map-marker</v-icon>
          </v-list-item-action>

          <v-list-item-content>
            <v-list-item-title>{{ user.rechnungsadresse }}</v-list-item-title>
          </v-list-item-content>
        </v-list-item>
        <v-divider inset></v-divider>
        <v-list-item>
          <v-list-item-action>
            <v-icon>mdi-web</v-icon>
          </v-list-item-action>

          <v-list-item-content>
            <v-list-item-title
              ><l>{{ user.url }}</l></v-list-item-title
            >
          </v-list-item-content>
        </v-list-item>
      </v-list>
    </v-card>
  </v-col>
</template>

<script>
export default {
  data: () => ({}),
  props: {
    user: {
      type: Object,
    },
    dialogData: {
      type: Boolean,
    },
  },
  methods: {
    getFachrichtung(fachrichtung) {
      switch (fachrichtung) {
        case 'E':
          return 'Elektrotechnik/Elektronik';
        case 'M':
          return 'Maschinenbau';
        case 'IT':
          return 'Informationstechnologie';
        default:
          break;
      }
    },
  },
};
</script>
